*{
  font-family: Arial, Helvetica, sans-serif;
}
body{
  padding: 60px;
}

input{
  color: gray;
  font-size: 1.3rem;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;


}